import React from "react";
import { stack as Menu } from "react-burger-menu";
import Link from "next/link";
import { withRouter } from "next/router";
import { BACKEND_URL } from "../env-config";

const sidebar = ({ router, props }) => {
  return (
    <Menu {...props}>
      <div className="menu-item">
        <img src="/static/images/acn-white.png" className="logo" />
      </div>
      <hr />
      <div className={`menu-item ${router.route == "/" ? "active" : ""}`}>
        <Link href={`${BACKEND_URL}/`}>Home</Link>
      </div>
      <div
        className={`menu-item ${router.route == "/materials" ? "active" : ""}`}
      >
        <Link href={`${BACKEND_URL}/materials`}>Learning Materials</Link>
      </div>
      <div
        className={`menu-item ${router.route == "/feedback" ? "active" : ""}`}
      >
        <a href={`${BACKEND_URL}/feedback`}>Feedback</a>
      </div>
      <div className={`menu-item ${router.route == "/faqs" ? "active" : ""}`}>
        <Link href={`${BACKEND_URL}/faqs`} prefetch>
          FAQs
        </Link>
      </div>
      <div
        className={`menu-item ${router.route == "/analytics" ? "active" : ""}`}
      >
        <Link href={`${BACKEND_URL}/analytics`}>Analytics</Link>
      </div>
    </Menu>
  );
};

export default withRouter(sidebar);
